import React, { Component } from 'react';
import img from './burger.jpg';
import img1 from './weather.png'; 
import img2 from './fitness.png';







export default class About extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                <div className="col-md-12">
                    <div className="about-desc">
                    <span className="heading-meta">About Me</span>
                    <h2 className="colorlib-heading">Who Am I?</h2>
                    <p>I am a Front-End Web Developer, UI/UX designer, and a Computational Linguist! I'm interested in learning languages and programming languages are no exceptions! </p>
                    <p> I got interested in UI/UX design in the "Human-Computer Interface" class at the university. Then, I decided to take a Full Stack Web Development Course and spent some time to teach myself UI and UX!</p>
                    <p>A fun fact about me?! My family and friends call me "Tara"! </p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        <section className="colorlib-about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">What I do?</span>
                <h2 className="colorlib-heading">Here are some of my expertise</h2>
            </div>
            </div>
            <div className="row row-pt-md">
            <div className="col-md-4 text-center animate-box">
                <div className="services color-1">
                <span className="icon">
                    <i className="icon-bulb" />
                </span>
                <div className="desc">
                    <h3>Web Development </h3>
                    <p>I have experience doing projects using JavaScript,React,HTML,CSS. You can take a look at my projects in my Github Repo!</p>
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-3">
                <span className="icon">
                    <i className="icon-phone3" />
                </span>
                <div className="desc">
                    <h3>User Experience Design</h3>
                    <p>Coming from CS background, and taking a mandatory UX design course at the university, I have a good grasp of the fundamental concepts in UX design.</p>
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                <span className="icon">
                    <i className="icon-data" />
                </span>
                <div className="desc">
                    <h3>Natural Language Processing</h3>
                    <p>I am involved in different NLP projects as a volunteer researcher! Currently, I am working on an online lexicon for an under-documented language. </p>
                </div>
                </div>
            </div>

            </div>
        </div>




        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <h2 className="colorlib-heading">some of my favorite mini projects</h2>
            </div>
            </div>
            <div className="row row-pt-md">
            <div className="col-md-4 text-center animate-box">
                <div className="services color-1">
                <div className="desc">
                    <h3>Eat The Burger!</h3>
                    <p>A very fun restaurant app using Node Express Handlebars!</p>
                    <img src={process.env.PUBLIC_URL + "/images/burger.jpg"}/>
                    <p> </p>
                    <p><a className="btn btn-primary btn-learn" href="https://tarazin-burger-app.herokuapp.com" target="_blank" rel="noopener noreferrer">Enjoy!</a></p>

                                       
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-3">
                <div className="desc">
                    <h3>Weather Dashboard</h3>
                    <p> Here is a 5-day forecast App based on OpenWeather API! </p>
                    <img src={process.env.PUBLIC_URL + "/images/weather.png"}/>
                    <p></p>
                    <p><a className="btn btn-primary btn-learn" href="https://tarazin.github.io/weather-api" target="_blank" rel="noopener noreferrer">Forecast!</a></p>
                    
                    
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">

                <div className="desc">
                    <h3>Fitness Tracker</h3>
                    <p> Using Nosql database, this fitness tracker helps you plan your workout routine! </p>
                    <img src={process.env.PUBLIC_URL + "/images/fitness.png"}/>
                    <p><a className="btn btn-primary btn-learn" href="https://floating-plains-80501.herokuapp.com" target="_blank" rel="noopener noreferrer">Track it!</a></p>
                    
                </div>
                </div>
            </div>

            </div>
        </div>

        

        </section>
      </div>
    )
  }
}